import React from "react";
import styled from "styled-components";
import queryString from "query-string";
import { useLocation } from "react-router-dom";
const SuccessPayment = () => {
  const location = useLocation();
  const { trackingNumber, orderId, message } = queryString.parse(
    location.search
  );
  return (
    <SuccessStyle>
      <SuccessHeader>
        <HeaderTitle> </HeaderTitle>
      </SuccessHeader>
      <SuccessMessage>{message}</SuccessMessage>
      <FactorTitle></FactorTitle>
      <SuccessContent>
        <SuccessList>
          <SuccessListItem>
            <Key>  </Key>
            <Value>{trackingNumber}</Value>
          </SuccessListItem>
          <SuccessListItem>
            <Key> </Key>
            <Value>{orderId}</Value>
          </SuccessListItem>
          <SuccessListItem>
            <Key> </Key>
            <Value></Value>
          </SuccessListItem>
        </SuccessList>
        <SuccessDesc>
                     
        </SuccessDesc>
      </SuccessContent>
    </SuccessStyle>
  );
};

const SuccessStyle = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
`;
const SuccessHeader = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 5px 1px rgb(64 60 67 / 16%);
  width: 100%;
`;

const HeaderTitle = styled.h4`
  color: #333;
  font-size: 20px;
  @media (max-width: 500px) {
    font-size: 14px;
  }
`;

const SuccessMessage = styled.p`
  font-size: 16px;
  color: #333;
  margin-top: 3rem;
  @media (max-width: 500px) {
    font-size: 14px;
    text-align: center;
  }
`;

const FactorTitle = styled.p`
  color: #6980ff;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #333;
  width: 150px;
  text-align: center;
`;

const SuccessContent = styled.div`
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #6980ff;
  /* border-radius: 10px; */
  padding: 4rem;

  @media (max-width: 800px) {
    width: 100%;
    padding: 2rem;
    border-top-left-radius: 20% !important;
  }
  @media (max-width: 500px) {
    padding: 2rem;
  }
`;

const SuccessList = styled.ul`
  width: 50%;
  display: flex;
  flex-direction: column;
  list-style: none;
  /* margin: 0; */
  padding: 0;
  @media (max-width: 800px) {
    width: 70%;
    margin: 2rem;
  }
`;

const SuccessListItem = styled.li`
  width: 100%;
  display: flex;
  justify-content: space-between;
`;

const Key = styled.p`
  color: #fff;
  font-size: 14px;
`;

const Value = styled.p`
  color: #fff;
  font-size: 14px;
`;

const SuccessDesc = styled.p`
  color: #fff;
  font-size: 14px;
  @media (max-width: 500px) {
    font-size: 12px;
  }
`;

export default SuccessPayment;
