import React from "react";
import queryString from "query-string";
import { useLocation } from "react-router-dom";
import styled from "styled-components";
import { sendPayment } from "../services/api";

const ErrorPayment = () => {
  const location = useLocation();
  const { amount, phoneNumber, category, nationalId, description, id } =
    queryString.parse(location.search);

  const handleRedirect = (data) => {
    const redirect = data?.data?.result?.redirectURL;
    window.location.replace(redirect);
  };

  const requestPayment = async () => {
    const paymentData = {
      amount,
      phoneNumber,
      category,
      nationalId,
      description,
    };
    const { data } = await sendPayment(JSON.stringify(paymentData));
    if (data) {
      handleRedirect(data);
    }
    // fetch(URL, {
    //   method: "POST",
    //   headers: {
    //     "Content-Type": "application/json",
    //     // Referer: "hive1400.ir",
    //   },
    //   body: JSON.stringify(data),
    //   redirect: "follow",
    // })
    //   .then((response) => response.json())
    //   .then((json) => handleRedirect(json))
    //   .catch((error) => console.log("error", error));
  };

  return (
    <ErrorStyle>
      <ErrorHeader>
        <HeaderTitle> </HeaderTitle>
      </ErrorHeader>
      <ErrorMessage>      </ErrorMessage>
      <ErrorContent>
        <ErrorTitle>
               <br />
                
        </ErrorTitle>
        <Button onClick={requestPayment}>   </Button>
      </ErrorContent>
    </ErrorStyle>
  );
};

const ErrorStyle = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
`;

const ErrorHeader = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 5px 1px rgb(64 60 67 / 16%);
  width: 100%;
`;

const HeaderTitle = styled.h4`
  color: #333;
  font-size: 20px;
  @media (max-width: 500px) {
    font-size: 14px;
  }
`;

const ErrorMessage = styled.p`
  font-size: 16px;
  color: #333;
  margin-top: 3rem;
  @media (max-width: 500px) {
    font-size: 14px;
    text-align: center;
  }
`;

const ErrorContent = styled.div`
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #6980ff;
  padding: 4rem;
  margin-top: 2rem;
  @media (max-width: 800px) {
    width: 100%;
    padding: 2rem;
    border-top-left-radius: 20% !important;
  }
  @media (max-width: 500px) {
    padding: 2rem;
  }
`;

const ErrorTitle = styled.p`
  font-size: 16px;
  color: #fff;
  text-align: center;
`;

const Button = styled.p`
  padding: 0.5rem 1rem;
  color: #fff;
  background-color: #acb1c4;
  border-radius: 5px;
  cursor: pointer;
`;

export default ErrorPayment;
